.intro__wrapper {
  position: relative;
  padding: 0 1.25rem;
  margin: auto;

  @include tablet {
    padding: 0 1.875rem;
  }

  @include desktop {
    padding: 0 1rem;
    max-width: 1624px;
  }
  @include bgdesktop {
    padding: 0 1.875rem;
    max-width: 1740px;
  }
}

.intro__container {
  position: relative;
  margin-bottom: 2.5rem;

  @include tablet {
    margin-bottom: 2rem;
  }

  @include desktop {
    margin-bottom: 5rem;
  }

  @include bgdesktop {
    margin-bottom: 7.1875rem;
  }
}

.intro__scroll {
  grid-column: span 1;
  position: relative;
  overflow: hidden;
  display: none;

  z-index: 4;

  @include bgdesktop {
    display: block;
  }

  .intro__scroll-content {
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 91%;
    width: 15rem;
    transform: rotate(-90deg);
    transform-origin: 0;
  }
  .intro__scroll-icon {
    transform: rotate(180deg);
  }
  .intro__scroll-text {
    padding-left: 1rem;
  }
}

.intro__footer {
  position: static;
  padding: 2rem 0;
  width: 100%;
  z-index: 4;

  @include tablet {
    padding: 0;
    margin: 0 0 2rem;
  }

  @include desktop {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0 1rem;
    margin: 0;
    max-width: 1592px;
  }

  @include bgdesktop {
    padding: 0 1.875rem;
    max-width: 1680px;
  }
}

.intro__social {
  grid-column: span 2;

  &-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    max-width: 5.125rem;
    li {
      a {
        &:hover {
          svg {
            path {
              fill: $hover-green;
            }
          }
        }
      }
    }
  }

  @include desktop {
    grid-column: span 1;
  }
}

.intro__controls {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-column: span 2;

  @include tablet {
    grid-column: span 10;
  }

  @include desktop {
    padding-right: 2rem;
    grid-column: span 3;
  }

  .previous, .next {
    cursor: pointer;

    &:hover {
      svg path, svg line {
        stroke: $basic-green;
      }
    }
  }

  .previous {
    padding-right: 1rem;
  }
}
