.slider {
  grid-column: span 4;

  height: 45rem;
  overflow: hidden;

  @include tablet {
    grid-column: span 12;

    height: 32rem;
  }

  @include desktop {
    grid-column: span 11;

    height: 88vh;
    min-height: 58.25rem;
    max-height: 58.25rem;
  }

  @include bgdesktop {

  }

  .slider-item {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    opacity: 0;

    display: flex;
    flex-direction: column;

    @include tablet {
      display: block;
      transition-delay: 1.4s;
      transition-duration: 0s;
      transition-property: opacity;
      will-change: opacity, transform;
    }

    &:not(.active) {
      @include tablet {
        animation-fill-mode: forwards;
        animation-delay: 0.5s;
        animation-name: slideTop;
        animation-duration: 0.9s;
        animation-timing-function: cubic-bezier(0.18, 0.54, 0.52, 0.93);
        pointer-events: none;
      }

      .slider__title {
        p {
          @include tablet {
            animation-name: cutTextUp;
            animation-duration: 0.45s;
            animation-timing-function: ease-in;
            animation-fill-mode: forwards;
          }
        }
      }

      .slider__button {
        @include tablet {
          animation-name: slideleft;
          animation-duration: 0.4s;
          animation-timing-function: ease-in-out;
          animation-fill-mode: forwards;
        }
      }
    }

    &.active {
      transition-delay: 0s;
      opacity: 1;

      @include tablet {
        transform: translateY(-100%);
        animation-fill-mode: forwards;
        animation-delay: 0.5s;
        animation-name: slideBotom;
        animation-duration: 0.8s;
        animation-timing-function: cubic-bezier(0.18, 0.54, 0.52, 0.93);
      }

      .slider__text {
        .slider__title {
          p {
            @include tablet {
              opacity: 0;
              animation-delay: 1.1s;
              animation-name: cutTextDown;
              animation-duration: 0.8s;
              animation-timing-function: ease;
              animation-fill-mode: forwards;
            }
          }
        }

        .slider__button {
          z-index: 1;

          @include tablet {
            opacity: 0;
            animation-delay: 1.3s;
            animation-name: slideRight;
            animation-duration: 0.7s;
            animation-timing-function: cubic-bezier(0.83, 0, 0.17, 1);
            animation-fill-mode: forwards;
          }
        }
      }
    }
  }

  .slider__text {
    grid-column: span 4;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;


    @include tablet {
      grid-column: span 5;
    }

    @include desktop {
      grid-column: span 4;
      padding-right: 3rem;
    }

    .slider__title {
      padding-top: 2rem;
      margin-bottom: 4rem;

      @include tablet {
        padding: 0;
      }

      span {
        will-change: auto;
        font-size: 1.875rem;
        line-height: 2.5rem;
        font-weight: 500;
        color: $black;

        @include desktop {
          font-size: 2.5625rem;
          line-height: 3.125rem;
        }
      }
    }

    .slider__button {
      display: flex;
      align-items: center;
      height: 45px;
      cursor: pointer;

      &-link {
        position: relative;
        display: flex;
        align-items: center;
        padding: 12px 20px 0 18px;
        font-size: 1.125rem;
        font-weight: 700;
        color: #1D1D1D;

        &:before {
          content: "";
          position: absolute;
          display: block;
          top: 0;
          left: 0;
          width: 50%;
          height: 45px;
          background-color: $basic-green;
          transition: all .2s ease-in-out;
          z-index: -1;
        }

        &:hover {
          &:before {
            width: 100%;
          }
        }
      }
    }
  }

  .slider__image {
    grid-column: span 4;
    width: 100%;
    height: 17.25rem;
    max-width: 59rem;
    overflow: hidden;

    @include tablet {
      grid-column: span 7;
      height: 32rem;
    }

    @include desktop {
      grid-column: span 8;
      height: 58.25rem;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.slider__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;

  @include tablet {
    display: grid;
  }
}

.slider--first-load {
  .slider-item.active {
    opacity: 0;
    animation-name: fadeIn;
    animation-delay: .3s;
    animation-duration: .4s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
    transform: translateY(0);
  }
  .slider-item.active .slider__text > p {
    animation-name: fadeIn;
    animation-delay: 0.5s;
    animation-duration: 1s;
  }
}
